<template>
    <div class="page-template">
        <el-card
            class="card"
            :body-style="{ padding: '0px'}"
            @click.native="$router.push('/page/home_page')"
        >
            <div class="card-emty df ffcn jcc" >
                <i class="add el-icon-plus" ></i>
                <span class="add-text">点击创建空白模板</span>
            </div>
        </el-card>

        <el-card
            :body-style="{ padding: '0px'}"
            class="card"
            v-for="(item, index) in 6"
            :key="index"
        >
            <img
                src="~@/assets/img/5e13799d5774ea9f098150d61336e08a4f957880.png@518w_1e_1c.png"
                class="card-image"
            />
            <div>
                <div class="describe">端午节端午节端午节端午节端午节端午节端午节</div>
                <div class="setting-area">
                    <el-tooltip content="编辑" placement="top">
                        <i class="el-icon-edit"></i>
                    </el-tooltip>
                    <el-tooltip content="发布" placement="top">
                        <i class="iconfont icon-fabu"></i>
                    </el-tooltip>
                    <el-tooltip content="删除" placement="top">
                        <i class="iconfont icon-shanchu"></i>
                    </el-tooltip>
                </div>
            </div>
        </el-card>
    </div>
</template>

<script>
export default {
    methods: {
        /* TODO: 发布 */
        release() {
            this.$confirm("确定发布到 ...？", "提示", {
                cancelButtonText: "确定",
                cancelButtonText: "取消",
                type: "info"
            })
                .then(res => {
                    console.log(res);
                    // ...
                    this.$message({
                        type: "success",
                        message: "发布成功!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消发布"
                    });
                });
        },
        /* TODO: 删除 */
        remove() {
            this.$confirm("此操作将永久删除该模板, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    // ...
                    this.$message({
                        type: "success",
                        message: "删除成功!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        }
    }
};
</script>

<style scoped>
.page-template {
    width: 100%;
}

.card-emty {
    height: 303px;
    text-align: center;
    position: relative;
    background-color: #fff;
}

.card-emty > i {
    font-size: 42px;
    color: rgba(0, 0, 0, 0.45);
    margin-bottom: 15px;
}

.card-emty > span {
    font-size: 18px;
    color: rgba(0, 0, 0, 0.45);
}

.card {
    cursor: pointer;
    margin: 20px 0 0 30px;
    display: inline-block;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1) !important;
    border: 1px solid #e8e8e8;
}

.card-image {
    height: 170px;
    display: block;
}

.describe {
    padding: 20px;
    color: rgba(0, 0, 0, 0.45);
    height: 42px;
    overflow: hidden;
}

.setting-area {
    display: flex;
    padding: 14px;
    border-top: 1px solid #e8e8e8;
}

.setting-area > i {
    flex: 1;
    text-align: center;
    font-size: 20px;
    color: rgba(0, 0, 0, 0.45);
}

.setting-area > i + i {
    border-left: 1px solid #e8e8e8;
}

/* ELEMENT_UI */
.page-template /deep/ .el-card {
    width: 260px;
}
</style>



